import Vue from 'vue'
import VueRouter from 'vue-router'
import IMain from '@/components/IMain'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'iMain',
    component: IMain,
    props:true
  },

]

const router = new VueRouter({
  mode:'history',
  routes
})

export default router
