<template>
  <div>
    <my-header></my-header>

    <div class = "galery-wrapper">
      <h1 class = "header-galery">Картины эпохи Возрождения</h1>
      <div class = "product-wrapper">
        <div class = "product" v-for = "product in list" v-bind:key = "product.id">
          <img class = "product__img" v-bind:src = "product.image" alt = "Изображение товара" width = "280"
               height = "160">
          <div class = "product__content">
            <h2 class = "product__title">{{ product.title }}</h2>
            <div class = "product__price-wrapper">
              <div class = "product__price">
                <p class = "product__old-price">{{ product.oldPrice }}</p>
                <p class = "product__new-price">{{ product.newPrice }}</p>
              </div>
              <button v-if = "product.busy" class = "product__button"> Loading...</button>
              <button v-else-if = "product.inCart" class = "product__button product__button--in-cart"> В корзине
              </button>
              <button v-else class = "product__button" type = "button" @click = "addToCart(product.id)">
                Купить
              </button>

            </div>
          </div>
        </div>
        <div class = "product product-disable">
          <img class = "product__img" :src = "require(`/public/static/images/painting-lesson.jpg`)"
               alt = "Изображение товара" width = "280"
               height = "160">
          <div class = "product__wrapper-description">
            <h2 class = "product__title">«Урок анатомии» Рембрандт</h2>
            <p class = "product__description">Продана на аукционе</p>
          </div>
        </div>
      </div>
    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
import MyHeader from "@/components/Header";
import MyFooter from "@/components/IFooter"
import axios from 'axios'

export default {
  data() {
    return {
      products: [],
      blocked: [],
      cart: [],
    }
  },
  components: {
    MyHeader,
    MyFooter
  },
  computed: {
    list() {
      return this.products.map(product => ({
        ...product,
        busy: this.blocked.indexOf(product.id) !== -1,
        inCart: this.cart.indexOf(product.id) !== -1
      }))
    }
  },
  watch: {
    cart(value) {
      localStorage.setItem('cart', JSON.stringify(value));
    }
  },
  mounted() {
    if (localStorage.getItem('cart')) {
      try {
        this.cart = JSON.parse(localStorage.getItem('cart'));
      } catch (e) {
        localStorage.removeItem('cart');
      }
    }
  },
  methods: {
    async addToCart(id) {
      this.blocked.push(id)
      axios.post('https://reqres.in/api/products/3', this.cart, {
        headers: {'Content-Type': 'application/json'}
      })
          .then(() => {
            this.cart.push(id);
          })
          .catch(error => {
            console.log(error.response)
          })
          .finally(() => {
            this.blocked = this.blocked.filter(blockedId => blockedId !== id);
          })
    }
  },
  created: function () {
    axios.get('./static/products.json')
        .then((response) => {
          this.products = response.data.products.map(product => ({
            ...product,
            busy: false,
          }))
          console.log(this.products);
        })
        .catch(e => {
          this.errors.push(e)
        })

  }
}
</script>

